import {db} from '../firebase'

const usePagination = () => {
    const setCurrentPage = (element, pages) => {
        const val = +element.target.value;
        if(val > pages) {
            return pages;
        }

        if(val < 1) {
            return 1;
        }

        
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        })
        
        return val;
    }

    const updateActualPage = (action, actualPage, pages) => {
        let actual = actualPage;

        if(action === 'increment') {
            actual += 1
        } else {
            actual -= 1
        }

        if(actual > pages) {
            return pages;
        }

        if(actual < 1){
            return 1;
        }

        if(window.innerWidth < 715) {
            window.scrollTo({
                top: 200,
                behavior: 'smooth'
            })
        }
        return actual;
    }

    const amount = async(url, perPage) => {
        let pages;
        await db.collection(url).get().then(
            (snapshot) => {
                pages = snapshot.docs.length / perPage;
            });
        return pages;
    }

    const fetch = async (url, actualPage, perPage) => {
        let partial = null;

        await db.collection(url)
        .orderBy("id", "asc")
        .startAt(actualPage)
        .limit(perPage)
        .get()
        .then(data => {
            partial = data.docs.map(doc => doc.data());
        })
        return partial
    }

    return {
        amount: amount,
        fetch: fetch,
        setCurrentPage: setCurrentPage,
        updateActualPage: updateActualPage
    }
}

export default usePagination;